import React, { Component } from 'react';

import Header from 'header';
import Network from 'network';

import './stylesheet.scss';


export default class Main extends Component {

    constructor(props) {
        super(props);

        this.state = {
            networks: [
                {
                    "resourceType": "Network",
                    "_id": "/network/rectalcancer",
                    "_links": {
                        "_self": "/network/rectalcancer",
                        "_collection": "/network",
                        "owner": "/user/1"
                    },
                    "_excluded": [
                        "json"
                    ],
                    "id": "rectalcancer",
                    "name": "Rectal cancer"
                }
                    ],
        }
    }


    render() {
        var { networks } = this.state;

        return (
            <div className="Main">
                <Header
                    networks={networks}
                    network_id={"rectalcancer"}
                    onNetworkSelected={this.onNetworkSelected}
                    {...this.props}
                    />

                    <Network
                        key={"/network/rectalcancer"}
                        network_id={"rectalcancer"}
                        {...this.props}
                    />

            </div>
        )
    }
}
