import axios from 'axios';
import createAuthRefreshInterceptor from 'axios-auth-refresh';


class Api {
    constructor() {
        this._axios = null;
        this._user = false; }

    /**
     * Initialize the API.
     */
    init(config) {
        // Setup axios to use JWT and automatically refresh if necessary.
        const urlParams = new URLSearchParams(window.location.search)
        const sessionToken = urlParams.get('session_token')
    
        this._config = {
            withCredentials: true,
            ...config
        };
        this._axios = axios.create(config);

        this._axios.interceptors.request.use(request => {
            request.params ={ session_token: sessionToken, ...request.params}
            return request;
        });

        createAuthRefreshInterceptor(
            this._axios,
            (failedRequest) => this.refreshAuthLogic(failedRequest)
        );

        // Make axios.request available for flexibility
        this.request = this._axios.request;
    }



    _request(config) {
        console.log('Api::request()', config);
        return this._axios.request(config).then(r => r.data);
    }

    /**
     * Return the API version
     */
    getVersion() {
        return this._request({
            url: '/version',
            method: 'GET',
        });
    }

    getSession() {
        return this._request({
            url: '/session',
            method: 'GET',
            withCredentials: true,
        });
    }

    getNetworks() {
        console.log('Api::getNetworks()', );

        return this._request({
            url: '/network',
            method: 'GET',
        });
    }

    getNetwork(id) {
        return this._request({
            url: `/network/${id}`,
            method: 'GET',
            // params: {summary: false}
        });
    }

    saveNetwork(id, network) {
        return this._request({
            url: `/network/${id}`,
            method: 'POST',
            data: network,
        });
    }

    queryNetwork(id, parameters) {
        var query = {
            id: id,
            query: {
                ...parameters
            }
        };

        return this._request({
            url: `network/_query`,
            // params: parameters,
            data: query,
            method: 'POST',
        })
    }
}

export const api = new Api();
export default api;